<template>
  <v-flex xs12>
    <v-tabs right class="elevation-3">
      <v-tab>
        Список&nbsp;&nbsp;<v-icon>list</v-icon>
      </v-tab>

      <br>
      <v-tab-item :key="1">
        <v-list>
          <v-list-group
            v-model="item.active"
            v-for="item in professionList"
            :key="item.name"
            no-action
          >
            <v-list-tile slot="activator">
              <v-list-tile-content>
                <v-list-tile-title>{{ item.name }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile v-for="subItem in item._proffs" :key="subItem.name" @click="">
              <v-list-tile-content>
                <v-list-tile-title>{{ subItem.name }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
        </v-list>
      </v-tab-item>
      <v-tab-item :key="2">
        <v-card flat>
          <v-container fluid grid-list-md>
              <v-data-iterator
                content-tag="v-layout"
                row
                wrap
                :items="professionList"
              >
                <v-flex
                  slot="item"
                  slot-scope="props"
                  xs12
                  sm6
                  md4
                  lg3
                >
                  <v-card>
                    <v-card-title><h4>{{ props.item.name }}</h4></v-card-title>
                    <v-divider/>
                    <v-list dense>
                      <v-list-tile>
                        <v-list-tile-content>Описание</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ props.item.about }}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content>Количество программ обучения:</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ props.item.countPrograms }}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content>Количество университетов</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ props.item.countUniversities }}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content>Стоимость обучения</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ props.item.priceEducation}}</v-list-tile-content>
                      </v-list-tile>
                      <v-list-tile>
                        <v-list-tile-content>Рейтинг</v-list-tile-content>
                        <v-list-tile-content class="align-end">{{ props.item.rate}}</v-list-tile-content>
                      </v-list-tile>
                    </v-list>
                  </v-card>
                </v-flex>
              </v-data-iterator>
          </v-container>
        </v-card>
      </v-tab-item>
    </v-tabs>
  </v-flex>
</template>
<script>
import {
  mapGetters, mapActions
}
  from 'vuex'
export default {
  name: 'ProfessionList',
  computed: {...mapGetters({
    professionList: 'profession/getProfessionList'
  })
  },
  data: function () {
    return {}
  },
  methods: {...mapActions({
    orderList: 'professionlistOrder'
  })
  }
}
</script>



